.i {
  display: flex;
  height: 100vh;
  
}

.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1;
  position: relative
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
}

.i-name {
  font-size: 60px;
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

 
@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  /* 50% {
    transform: translateY(-100px);
  } */
  /* 75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }  */
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #59b256;
  display: flex;
  align-items: center;
}

.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

.i-img {
  width: 85%;
  height: 93%;
  object-fit: cover;
  position: absolute;
  right: 0;
  top: 40px;
}

.i-bg {
  clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: #59b256;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}




@media all and (max-width: 480px) {
  .i {
    flex-direction: column;
   /* margin-left: 5px; */
    /* padding: 0; */
  }

  .i-left-wrapper {
  
    padding: 10px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }
  .i-bg {
    display: none;
  }
  .i-img {
    border-radius: 50%;
    background-color: #59b256;
  }

   .i-disc{
    font-size: small;
  } 
  .i-intro{
    font-size: medium;
  }
  .i-name{
    font-size: medium;
  }
  .i-title-item{
    font-size: medium;
  }
  .i-title-wrapper{
    height: 50%;
  }
  .i-img {
    width: 76%;
    height: 88%;
    right: 30px;
  }
  
}